.stars {
  display: flex;

  &__star {
    background-image: url("../images/star.svg");
    width: $star-size;
    height: $star-size;
    background-position: center;
    background-repeat: no-repeat;

    &:not(:last-child) {
      margin-right: 4px;
    }
  }
  @for $n from 1 through 5 {
    &--#{$n} &__star:nth-child(-n + #{$n}) {
      background-image: url("../images/star-active.svg");
    }
  }
}
